function sliderWorks() {
    new Swiper(".works .mySwiper",{
        slidesPerView: 4,
        spaceBetween: 8,
        grid: {
            rows: 2,
        },
        navigation: {
            nextEl: ".works .swiper-button-next",
            prevEl: ".works .swiper-button-prev"
        },
        pagination: {
            el: ".works .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                grid: {
                    rows: 1,
                },
            },
            577: {
                slidesPerView: 2,
                grid: {
                    rows: 1,
                },
            },
            769: {
                slidesPerView: 3,
                grid: {
                    rows: 1,
                },
            },
            993: {
                slidesPerView: 4,
                grid: {
                    rows: 2,
                },
            }
        }
    })
}

$(document).ready(function() {
    sliderWorks();

    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

})